import React from "react";
import { FaLinkedin } from "react-icons/fa6";
import { GrInstagram } from "react-icons/gr";
import { BsFacebook, BsTwitter } from "react-icons/bs";

export const SocialLinks = () => {
  const links = [
    {
      id: 1,
      child: (
        <>
          <FaLinkedin />
        </>
      ),
      href: "https://in.linkedin.com/in/rushibutani",
    },
    {
      id: 2,
      child: (
        <>
          <GrInstagram />
        </>
      ),
      href: "https://www.instagram.com/rushibutani/",
    },
    {
      id: 3,
      child: (
        <>
          <BsTwitter />
        </>
      ),
      href: "https://twitter.com/rushibutani_",
    },
    {
      id: 4,
      child: (
        <>
          <BsFacebook />
        </>
      ),
      href: "https://www.facebook.com/rushibutani",
    },
  ];
  return (
    <>
      <ul className="flex footer-icon-container">
        {links.map(({ id, child, href, download }) => (
          <li key={id} className="footer-icon">
            <a href={href} download={download} target="_blank" rel="noreferrer">
              {child}
            </a>
          </li>
        ))}
      </ul>
    </>
  );
};
